@font-face {
  font-family: 'iransans';
  src:url('/assets/fonts/iransans/IRANSansWeb.eot');
  src:url('/assets/fonts/iransans/IRANSansWeb.eot?#iefix') format('embedded-opentype'),
      url('/assets/fonts/iransans/IRANSansWeb.ttf') format('truetype'),
      url('/assets/fonts/iransans/IRANSansWeb.woff') format('woff') ,
      url('/assets/fonts/iransans/IRANSansWeb.woff2') format('woff2');
}

body {
  margin: 0;
  padding: 0;
  background: #eeeeee;
  overflow-x: hidden;
}

* {
  font-family: 'iransans', sans-serif;
  font-weight: normal;
  font-size: .9rem;
}

html {
  /* Prevent text size change on orientation change. */
  -webkit-text-size-adjust: 100%;
}

#app {
  direction: rtl;
}

.component {
  margin-top: 72px;
  padding: 16px;
}

.avatar {
  position: absolute;
  top: 8px;
  right: 8px;
  margin: auto;
  height: auto;
}
.searchInput {
  width: 100%;
  height: 40px;
  padding: 0px 8px;
  border: none;
  border-radius: 2px;
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
  padding-right: 40px;
}

input:focus,
textarea:focus,
button:focus {
  outline: none;
}

.noSelect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}

.dialogTitle {
  text-align: right !important;
  padding: 16px !important;
  font-size: 1.2rem !important;
}

.dialogBody {
  text-align: right !important;
  padding: 16px !important;
  font-size: .9rem !important;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: white !important;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus
input:-webkit-autofill, 
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border:none !important;
  -webkit-text-fill-color: inherit !important;
  -webkit-box-shadow: 0 0 0px 1000px #FFFFFF inset;
  box-shadow: 0 0 0px 1000px #FFFFFF inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  -o-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

input::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  display: none !important;
  pointer-events: none;
  position: absolute;
  right: 0;
}

.pulse > button {
  overflow: initial;
  position: relative;
}

.pulse > button::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: inherit;
  border-radius: inherit;
  -webkit-transition: opacity .3s, -webkit-transform .3s;
  transition: opacity .3s, -webkit-transform .3s;
  -o-transition: opacity .3s, transform .3s;
  transition: opacity .3s, transform .3s;
  transition: opacity .3s, transform .3s, -webkit-transform .3s;
  -webkit-animation: pulse-animation 2s cubic-bezier(0.24, 0, 0.38, 1) infinite;
          animation: pulse-animation 2s cubic-bezier(0.24, 0, 0.38, 1) infinite;
  z-index: -1;
}

.snack {
  text-align: center;
}

.snack > div {
  margin-right: 210px !important;
}

@media only screen and (max-width: 992px) {
  .snack > div {
    margin-right: 0px !important;
  }
}
.drawerDropDownItem {
  text-align: right !important;
}

.truncate {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}



